export default class Singer {
    constructor ({title,id,name}) {
        this.title = title
        this.id = id
        this.name = name
        this.avatar = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${id}.jpg?m
        ax_age=2592000`
    }
}







